.header {
  display: flex;
  width: calc(100% - #{$sidebar-width});
  background-color: #0d0d0d;
  box-shadow: 0 1px 22px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  right: 0;
  z-index: $z-index-header;
}

.header__left {
  width: 60%;
}
.header__right {
  display: flex;
  width: 40%;
  justify-content: flex-end;
}
